* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.card-flex {
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: center;
          align-content: center;
  max-width: 800px;
  margin-bottom: 30px;
}

.card-box img {
    height: 150px;
    width: 150px;
}

.card-box {
    border: 2px solid red;
    margin: 10px;
    height: 154px;
    width: 154px;
    cursor: pointer;
}
.navbar {
  width: 100%;
  text-align: center;
  font-size: 10px;
  color: white;
  background-color: red;
  font-family: 'Coiny', cursive;
  padding-top: 20px;
  padding-bottom: 10px;
}

.jumbotron {
  height: 350px;
  background-image: url("http://www.keepaustingreen.com/wp-content/uploads/2019/01/truffula-tree.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-color: red;
}

h4 {
text-align: center;
font-family: 'Quicksand', sans-serif;
position: relative;
bottom: -80px;
color: white;
}

header {
  font-family: 'Coiny', cursive;
  font-size: 60px;
  text-align: center;
  color: white;
  position: relative;
  bottom: -90px;
}

.footer {
    width: 100%;
    height: 30px;
    background-color: red;
  }
.gameWrap { 
    display: -webkit-flex; 
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: center;
            align-content: center;
-webkit-flex-wrap: wrap;
        flex-wrap: wrap;
}
